import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatBottomSheetRef, MAT_BOTTOM_SHEET_DATA } from '@angular/material/bottom-sheet';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { HttpResponseModel } from '@models/http/response.model';
import { AuthenticationService } from '@services/authentication.service';

@Component({
  selector: 'app-otp',
  templateUrl: './otp.component.html',
  styleUrls: ['./otp.component.css']
})
export class OTPComponent implements OnInit {
  sse = '';

  vfg: FormGroup = new FormGroup({
    otp: new FormControl(
      '000000',
      [
        Validators.required,
        Validators.pattern(/^[0-9]{6}$/),
      ],
    ),
  });

  constructor(
    @Inject(MAT_BOTTOM_SHEET_DATA) public data: any,
    private $authentication: AuthenticationService,
    private $router: Router,
    private $bottomSheetRef: MatBottomSheetRef<OTPComponent>,
    private $snackBar: MatSnackBar,
  ) {
  }

  ngOnInit(): void {
  }


  verifyOTP(event: any): void {
    event.preventDefault();

    if (this.vfg.valid) {
      this.sse = '';
      this.vfg.disable();
      this.$authentication.verifyOTP(this.data.user.phone, this.vfg.value.otp).subscribe(
        () => {
          this.$authentication.updateUser(this.data.user).subscribe(
            (result: HttpResponseModel) => {
              this.vfg.enable();
              this.$authentication.detox();
              this.$snackBar.open(result.message, '', {
                duration: 3000,
              });
              this.$bottomSheetRef.dismiss();
              this.$router.navigate(['/home']);
            },
            (error: HttpResponseModel) => {
              this.vfg.enable();
              this.sse = error.message;
            }
          );
        },
        (error: HttpResponseModel) => {
          this.sse = error.message;
        }
      );
    }
  }

  dismiss(event: MouseEvent): void {
    this.$bottomSheetRef.dismiss();
    event.preventDefault();
  }
}
