import { AfterContentInit, Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { DepartmentModel } from '@models/department.model';
import { HttpResponseModel } from '@models/http/response.model';
import { RoleModel } from '@models/role.model';
import { AuthenticationService } from '@services/authentication.service';
import { ConfigService } from '@services/config.service';
import { DepartmentService } from '@services/department.service';
import { OTPComponent } from '@profile/otp/otp.component';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit, AfterContentInit {
  sse = '';

  roles: RoleModel[] = [];
  departments: DepartmentModel[] = [];

  pfg: FormGroup = new FormGroup({
    did: new FormControl(
      '',
      [
        Validators.required,
        Validators.pattern(/^[0-9]$/),
      ],
    ),
    name: new FormControl(
      '',
      [
        Validators.required,
        Validators.pattern(/^[A-Za-z\s]{1,50}$/),
      ],
    ),
    email: new FormControl(
      '',
      [
        Validators.required,
        Validators.email,
      ],
    ),
    designation: new FormControl(
      '',
      [
        Validators.required,
        Validators.pattern(/^.{1,30}$/),
      ],
    ),
    phone: new FormControl(
      '',
      [
        Validators.required,
        Validators.pattern(/^[0-9]{10}$/),
      ],
    ),
  });

  constructor(
    private $authentication: AuthenticationService,
    private $department: DepartmentService,
    private $bottomSheet: MatBottomSheet,
    private $snackBar: MatSnackBar,
    public config: ConfigService,
  ) {
    this.pfg.patchValue({
      ...this.$authentication.user,
    });
  }

  ngOnInit(): void {
  }

  ngAfterContentInit(): void {
    this.getOrg();
  }

  updateUser(event: any): void {
    event.preventDefault();

    if (this.pfg.valid) {
      this.sse = '';
      this.pfg.disable();
      this.$authentication.getOTP(this.pfg.value.phone).subscribe(
        (result: HttpResponseModel) => {
          this.pfg.enable();
          this.$bottomSheet.open(OTPComponent, {
            data: {
              user: this.pfg.value,
            },
            disableClose: true,
          });
        },
        (error: HttpResponseModel) => {
          this.pfg.enable();
          this.sse = error.message;
        }
      );
    }
  }

  getOrg(): void {
    this.getDepartments();
  }

  getDepartments(): void {
    this.$department.getDepartments()
      .subscribe(
        (departments: DepartmentModel[]) => {
          this.departments = departments;
        },
        (error: HttpResponseModel) => {
        }
      );
  }

  pickProfile(event: MouseEvent, profile: any): void {
    event.preventDefault();

    profile.click();
  }

  uploadProfile(event: any): void {
    event.preventDefault();

    if (event.target.files.length > 0) {
      const [file] = event.target.files;

      this.$authentication.uploadProfile(file).subscribe(
        (result: HttpResponseModel) => {
          this.$snackBar.open(result.message, '', {
            duration: 3000,
          });
        },
        (error: HttpResponseModel) => {
          this.$snackBar.open(error.message, '', {
            duration: 3000,
          });
        }
      );
    }
  }

  get userName(): string {
    return this.$authentication.user?.name || 'Untitled';
  }

  get roleLabel(): string {
    const role = this.$authentication.user?.role;
    if (role) {
      return role.label;
    } else {
      return '';
    }
  }

  get profilePath(): string {
    return this.$authentication.profilePath;
  }
}
